import React, { useState, useEffect, useRef } from 'react';
import { parseEmbedParams } from './utils/params';
import { useEmbedTheme } from './hooks/useEmbedTheme';
import EmbedShell from './components/EmbedShell';
import AdminApp from './admin/AdminApp';
import { supabase } from './supabaseClient';

function App() {
  const [hash, setHash] = useState(window.location.hash);

  useEffect(() => {
    const onHashChange = () => setHash(window.location.hash);
    window.addEventListener('hashchange', onHashChange);
    return () => window.removeEventListener('hashchange', onHashChange);
  }, []);

  // Admin route
  if (hash.startsWith('#/admin')) {
    return <AdminApp />;
  }

  // Existing embed flow
  return <EmbedFlow />;
}

// Keys that can be overridden by saved community settings
const THEME_KEYS = ['bg', 'surface', 'primary', 'accent', 'text', 'font'];
const BEHAVIOR_KEYS = ['count', 'difficulty', 'timer', 'leaderboard', 'category'];

function EmbedFlow() {
  const searchParams = new URLSearchParams(window.location.search);
  const { params: urlParams, errors } = parseEmbedParams(searchParams);
  const profileParam = searchParams.get('profile');

  // Track which params the URL explicitly provides
  const explicitKeys = useRef(
    new Set([...THEME_KEYS, ...BEHAVIOR_KEYS].filter(k => searchParams.has(k)))
  );

  const [mergedParams, setMergedParams] = useState(urlParams);
  const [settingsLoaded, setSettingsLoaded] = useState(!urlParams.community && !profileParam);

  // Helper: merge saved theme/behavior into params, respecting explicit URL overrides
  const mergeSaved = (savedTheme, savedBehavior) => {
    const explicit = explicitKeys.current;
    setMergedParams(prev => {
      const merged = { ...prev };
      for (const key of THEME_KEYS) {
        if (!explicit.has(key) && savedTheme[key] != null) {
          merged[key] = savedTheme[key];
        }
      }
      for (const key of BEHAVIOR_KEYS) {
        if (!explicit.has(key) && savedBehavior[key] != null) {
          merged[key] = savedBehavior[key];
        }
      }
      return merged;
    });
  };

  // Fetch settings: profile param > community settings fallback
  useEffect(() => {
    // If profile param is provided, load from embed_profiles by ID
    if (profileParam) {
      supabase
        .from('embed_profiles')
        .select('*')
        .eq('id', profileParam)
        .single()
        .then(({ data, error }) => {
          if (data && !error) {
            const profTheme = { ...(data.theme || {}), font: data.font || (data.theme || {}).font };
            mergeSaved(profTheme, data.behavior || {});
          }
          setSettingsLoaded(true);
        })
        .catch(() => setSettingsLoaded(true));
      return;
    }

    // Fallback: load from communities.settings.embed_theme
    if (!urlParams.community) return;

    supabase
      .from('communities')
      .select('settings')
      .eq('slug', urlParams.community)
      .single()
      .then(({ data }) => {
        if (data?.settings) {
          mergeSaved(
            data.settings.embed_theme || {},
            data.settings.embed_behavior || {},
          );
        }
        setSettingsLoaded(true);
      })
      .catch(() => setSettingsLoaded(true));
  }, [urlParams.community, profileParam]);

  // Apply theme CSS custom properties and load Google Font
  useEmbedTheme(mergedParams);

  // Show error if community is missing
  if (errors.length > 0 || !urlParams.community) {
    return (
      <div style={{
        padding: '2rem',
        fontFamily: 'sans-serif',
        color: '#F5F0EB',
        backgroundColor: '#0D0D0D',
        minHeight: '100vh',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        textAlign: 'center',
      }}>
        <div>
          <h2 style={{ marginBottom: '1rem' }}>Quiz Embed</h2>
          <p>Missing required parameter: <code>community</code></p>
          <p style={{ opacity: 0.6, marginTop: '0.5rem', fontSize: '0.875rem' }}>
            Add <code>?community=your-community-slug</code> to the URL.
          </p>
        </div>
      </div>
    );
  }

  // Wait for settings to load before rendering embed to avoid theme flash
  if (!settingsLoaded) {
    return (
      <div style={{
        backgroundColor: 'var(--embed-bg)',
        color: 'var(--embed-text)',
        fontFamily: 'var(--embed-font)',
        minHeight: '100vh',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
      }}>
        <div style={{ textAlign: 'center', opacity: 0.7 }}>Loading quiz...</div>
      </div>
    );
  }

  return <EmbedShell params={mergedParams} />;
}

export default App;
