import React, { useState } from 'react';

/**
 * AnnouncementBanner
 *
 * Displays pinned community announcements in a compact banner.
 * Supports cycling through multiple announcements with prev/next arrows.
 * Dismissible per session via React state.
 */
function AnnouncementBanner({ announcements }) {
  const [dismissed, setDismissed] = useState(false);
  const [currentIndex, setCurrentIndex] = useState(0);

  if (!announcements || announcements.length === 0 || dismissed) {
    return null;
  }

  const total = announcements.length;
  const current = announcements[currentIndex];

  const handlePrev = () => {
    setCurrentIndex((currentIndex - 1 + total) % total);
  };

  const handleNext = () => {
    setCurrentIndex((currentIndex + 1) % total);
  };

  return (
    <div style={styles.banner}>
      <div style={styles.content}>
        <div style={styles.textArea}>
          <span style={styles.title}>{current.title}</span>
          {current.body && (
            <span style={styles.body}>  {current.body}</span>
          )}
        </div>
        <div style={styles.controls}>
          {total > 1 && (
            <>
              <button onClick={handlePrev} style={styles.arrowBtn} aria-label="Previous announcement"></button>
              <span style={styles.indicator}>{currentIndex + 1} of {total}</span>
              <button onClick={handleNext} style={styles.arrowBtn} aria-label="Next announcement"></button>
            </>
          )}
          <button onClick={() => setDismissed(true)} style={styles.dismissBtn} aria-label="Dismiss announcements"></button>
        </div>
      </div>
    </div>
  );
}

const styles = {
  banner: {
    background: 'var(--embed-surface)',
    color: 'var(--embed-text)',
    borderLeft: '3px solid var(--embed-primary)',
    borderRadius: '4px',
    padding: '0.5rem 0.75rem',
    marginBottom: '0.75rem',
    fontSize: '0.85rem',
    lineHeight: '1.4',
  },
  content: {
    display: 'flex',
    alignItems: 'center',
    gap: '0.5rem',
  },
  textArea: {
    flex: 1,
    minWidth: 0,
    overflow: 'hidden',
    display: '-webkit-box',
    WebkitLineClamp: 2,
    WebkitBoxOrient: 'vertical',
  },
  title: {
    fontWeight: 700,
  },
  body: {
    opacity: 0.85,
  },
  controls: {
    display: 'flex',
    alignItems: 'center',
    gap: '0.25rem',
    flexShrink: 0,
  },
  indicator: {
    fontSize: '0.75rem',
    opacity: 0.6,
    whiteSpace: 'nowrap',
  },
  arrowBtn: {
    background: 'none',
    border: 'none',
    color: 'var(--embed-text)',
    cursor: 'pointer',
    fontSize: '1.1rem',
    padding: '0 0.2rem',
    lineHeight: 1,
    opacity: 0.7,
  },
  dismissBtn: {
    background: 'none',
    border: 'none',
    color: 'var(--embed-text)',
    cursor: 'pointer',
    fontSize: '0.8rem',
    padding: '0 0.25rem',
    lineHeight: 1,
    opacity: 0.5,
    marginLeft: '0.25rem',
  },
};

export default AnnouncementBanner;
