import React, { useState, useEffect, useCallback } from 'react';
import { supabase } from '../../supabaseClient';
import { hasCommunityRole, canManageMembers } from '../permissions';
import { sendInvitationEmail } from '../../utils/emailService';

const ROLE_COLORS = {
  owner: { bg: '#041E42', text: '#FFFFFF' },
  commissioner: { bg: '#1D4ED8', text: '#FFFFFF' },
  moderator: { bg: '#0D9488', text: '#FFFFFF' },
  member: { bg: '#54585A', text: '#FFFFFF' },
};

const ROLE_LABELS = {
  owner: 'Owner',
  commissioner: 'Admin',
  moderator: 'Moderator',
  member: 'Member',
};

// Roles that admins (commissioners) can assign
const ASSIGNABLE_ROLES_ADMIN = ['moderator', 'commissioner', 'member'];
// Roles that owners can assign
const ASSIGNABLE_ROLES_OWNER = ['moderator', 'commissioner', 'owner', 'member'];

function MembersTab({ community, communityRole, onToast }) {
  const [members, setMembers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState('');
  const [confirmRemove, setConfirmRemove] = useState(null); // member id being confirmed
  const [changingRole, setChangingRole] = useState(null); // member id being changed
  const [inviteEmail, setInviteEmail] = useState('');
  const [inviteMessage, setInviteMessage] = useState('');
  const [inviteSending, setInviteSending] = useState(false);

  const isOwner = hasCommunityRole(communityRole, 'owner');
  const canManage = canManageMembers(communityRole);

  const fetchMembers = useCallback(async () => {
    try {
      const { data, error } = await supabase
        .from('community_members')
        .select('*, profiles(username, id)')
        .eq('community_id', community.id)
        .order('joined_at', { ascending: false });
      if (error) throw error;
      setMembers(data || []);
    } catch (err) {
      onToast?.('Failed to load members: ' + err.message, 'error');
    } finally {
      setLoading(false);
    }
  }, [community.id, onToast]);

  useEffect(() => {
    fetchMembers();
  }, [fetchMembers]);

  const handleRoleChange = async (member, newRole) => {
    setChangingRole(member.id);
    try {
      const { error } = await supabase
        .from('community_members')
        .update({ role: newRole })
        .eq('id', member.id);
      if (error) throw error;
      onToast?.(`${member.profiles?.username || 'Member'} role updated to ${ROLE_LABELS[newRole]}`);
      await fetchMembers();
    } catch (err) {
      onToast?.('Failed to update role: ' + err.message, 'error');
    } finally {
      setChangingRole(null);
    }
  };

  const handleRemove = async (member) => {
    try {
      const { error } = await supabase
        .from('community_members')
        .delete()
        .eq('id', member.id);
      if (error) throw error;
      onToast?.(`${member.profiles?.username || 'Member'} removed from community`);
      setConfirmRemove(null);
      await fetchMembers();
    } catch (err) {
      onToast?.('Failed to remove member: ' + err.message, 'error');
    }
  };

  const copyToClipboard = (text, label) => {
    navigator.clipboard.writeText(text).then(() => {
      onToast?.(`${label} copied!`);
    }).catch(() => {
      onToast?.('Failed to copy', 'error');
    });
  };

  const handleSendInvite = async () => {
    const email = inviteEmail.trim();
    if (!email) return;
    if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email)) {
      onToast?.('Please enter a valid email address', 'error');
      return;
    }
    setInviteSending(true);
    try {
      const result = await sendInvitationEmail(
        email,
        community.name,
        community.invite_code,
        community.description || '',
        inviteMessage.trim()
      );
      if (result.success) {
        onToast?.(`Invitation sent to ${email}`);
        setInviteEmail('');
        setInviteMessage('');
      } else {
        onToast?.(`Failed to send invite: ${result.error}`, 'error');
      }
    } catch (err) {
      onToast?.('Failed to send invite: ' + err.message, 'error');
    } finally {
      setInviteSending(false);
    }
  };

  const filtered = members.filter(m => {
    if (!search.trim()) return true;
    const username = m.profiles?.username || '';
    return username.toLowerCase().includes(search.toLowerCase());
  });

  const inviteCode = community.invite_code || '';

  const getAssignableRoles = (targetMember) => {
    const roles = isOwner ? ASSIGNABLE_ROLES_OWNER : ASSIGNABLE_ROLES_ADMIN;
    return roles.filter(r => r !== targetMember.role);
  };

  const canChangeRole = (targetMember) => {
    if (!canManage) return false;
    // Can't change own role
    if (targetMember.user_id === supabase.auth.getUser?.()?.data?.user?.id) return false;
    // Only owner can change another owner's role
    if (targetMember.role === 'owner' && !isOwner) return false;
    return true;
  };

  const canRemoveMember = (targetMember) => {
    if (!canManage) return false;
    // Can't remove yourself
    if (targetMember.user_id === supabase.auth.getUser?.()?.data?.user?.id) return false;
    // Can't remove the owner unless you're the owner
    if (targetMember.role === 'owner') return false;
    return true;
  };

  if (loading) {
    return (
      <div style={{ textAlign: 'center', padding: '60px 20px', color: '#54585A' }}>
        Loading members...
      </div>
    );
  }

  return (
    <div>
      <h1 style={styles.title}>Members ({members.length})</h1>
      <p style={styles.subtitle}>Manage community members and roles</p>

      {/* Invite Code Section */}
      <div style={styles.card}>
        <div style={styles.cardHeader}>
          <h2 style={styles.cardTitle}>Invite Code</h2>
        </div>
        <div style={styles.cardBody}>
          <div style={{ display: 'flex', gap: '8px', alignItems: 'center' }}>
            <code style={styles.code}>{inviteCode}</code>
            <button
              onClick={() => copyToClipboard(inviteCode, 'Invite code')}
              style={styles.copyBtn}
            >
              Copy
            </button>
          </div>
          <div style={{ fontSize: '0.75rem', color: '#54585A', marginTop: '4px' }}>
            Share this code so others can join your community
          </div>
        </div>
      </div>

      {/* Invite by Email */}
      <div style={styles.card}>
        <div style={styles.cardHeader}>
          <h2 style={styles.cardTitle}>Invite by Email</h2>
        </div>
        <div style={styles.cardBody}>
          <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
            <input
              type="email"
              value={inviteEmail}
              onChange={e => setInviteEmail(e.target.value)}
              placeholder="Enter email address..."
              style={styles.input}
              onKeyDown={e => e.key === 'Enter' && !inviteSending && handleSendInvite()}
            />
            <textarea
              value={inviteMessage}
              onChange={e => setInviteMessage(e.target.value)}
              placeholder="Add a personal message (optional)"
              rows={2}
              style={{ ...styles.input, resize: 'vertical', minHeight: '48px' }}
            />
            <button
              onClick={handleSendInvite}
              disabled={inviteSending || !inviteEmail.trim()}
              style={{
                ...styles.copyBtn,
                background: '#041E42',
                color: '#FFFFFF',
                border: 'none',
                padding: '8px 16px',
                fontSize: '0.8125rem',
                fontWeight: 600,
                opacity: inviteSending || !inviteEmail.trim() ? 0.5 : 1,
                cursor: inviteSending || !inviteEmail.trim() ? 'default' : 'pointer',
                alignSelf: 'flex-start',
              }}
            >
              {inviteSending ? 'Sending...' : 'Send Invitation'}
            </button>
          </div>
          <div style={{ fontSize: '0.75rem', color: '#54585A', marginTop: '8px' }}>
            Sends a branded email with your invite code and a link to join
          </div>
        </div>
      </div>

      {/* Search */}
      <div style={{ marginBottom: '1rem' }}>
        <input
          type="text"
          value={search}
          onChange={e => setSearch(e.target.value)}
          placeholder="Search by username..."
          style={styles.input}
        />
      </div>

      {/* Members List */}
      <div style={styles.card}>
        <div style={styles.cardHeader}>
          <h2 style={styles.cardTitle}>
            {search.trim() ? `Showing ${filtered.length} of ${members.length}` : `All Members`}
          </h2>
        </div>
        <div style={{ padding: 0 }}>
          {filtered.length === 0 ? (
            <div style={{ padding: '32px 20px', textAlign: 'center', color: '#54585A', fontSize: '0.875rem' }}>
              No members found
            </div>
          ) : (
            <table style={styles.table}>
              <thead>
                <tr>
                  <th style={styles.th}>Username</th>
                  <th style={styles.th}>Role</th>
                  <th style={styles.th}>Joined</th>
                  {canManage && <th style={{ ...styles.th, textAlign: 'right' }}>Actions</th>}
                </tr>
              </thead>
              <tbody>
                {filtered.map(member => {
                  const username = member.profiles?.username || 'Unknown';
                  const role = member.role || 'member';
                  const roleColor = ROLE_COLORS[role] || ROLE_COLORS.member;
                  const joined = member.joined_at
                    ? new Date(member.joined_at).toLocaleDateString()
                    : '';

                  return (
                    <tr key={member.id} style={styles.tr}>
                      <td style={styles.td}>
                        <span style={{ fontWeight: 500, color: '#1F2937' }}>{username}</span>
                      </td>
                      <td style={styles.td}>
                        <span style={{
                          ...styles.badge,
                          backgroundColor: roleColor.bg,
                          color: roleColor.text,
                        }}>
                          {ROLE_LABELS[role] || role}
                        </span>
                      </td>
                      <td style={styles.td}>
                        <span style={{ color: '#54585A', fontSize: '0.8125rem' }}>{joined}</span>
                      </td>
                      {canManage && (
                        <td style={{ ...styles.td, textAlign: 'right' }}>
                          <div style={{ display: 'flex', gap: '6px', justifyContent: 'flex-end', alignItems: 'center', flexWrap: 'wrap' }}>
                            {canChangeRole(member) && (
                              <select
                                value={role}
                                onChange={e => handleRoleChange(member, e.target.value)}
                                disabled={changingRole === member.id}
                                style={styles.select}
                              >
                                <option value={role}>{ROLE_LABELS[role]}</option>
                                {getAssignableRoles(member).map(r => (
                                  <option key={r} value={r}>{ROLE_LABELS[r]}</option>
                                ))}
                              </select>
                            )}
                            {canRemoveMember(member) && (
                              <>
                                {confirmRemove === member.id ? (
                                  <div style={{ display: 'flex', gap: '4px', alignItems: 'center' }}>
                                    <button
                                      onClick={() => handleRemove(member)}
                                      style={styles.confirmRemoveBtn}
                                    >
                                      Confirm
                                    </button>
                                    <button
                                      onClick={() => setConfirmRemove(null)}
                                      style={styles.cancelBtn}
                                    >
                                      Cancel
                                    </button>
                                  </div>
                                ) : (
                                  <button
                                    onClick={() => setConfirmRemove(member.id)}
                                    style={styles.removeBtn}
                                  >
                                    Remove
                                  </button>
                                )}
                              </>
                            )}
                          </div>
                        </td>
                      )}
                    </tr>
                  );
                })}
              </tbody>
            </table>
          )}
        </div>
      </div>
    </div>
  );
}

const styles = {
  title: {
    color: '#041E42',
    fontSize: '1.5rem',
    marginBottom: '0.5rem',
    fontWeight: 600,
  },
  subtitle: {
    color: '#54585A',
    marginBottom: '1.5rem',
  },
  card: {
    background: '#FFFFFF',
    borderRadius: '8px',
    border: '1px solid #E5E7EB',
    marginBottom: '1.5rem',
    overflow: 'hidden',
  },
  cardHeader: {
    padding: '16px 20px',
    borderBottom: '1px solid #E5E7EB',
  },
  cardTitle: {
    margin: 0,
    fontSize: '1rem',
    fontWeight: 600,
    color: '#041E42',
  },
  cardBody: {
    padding: '20px',
  },
  code: {
    padding: '6px 12px',
    background: '#F5F5F7',
    borderRadius: '4px',
    fontSize: '0.875rem',
    fontFamily: 'monospace',
    letterSpacing: '1px',
  },
  copyBtn: {
    background: '#F5F5F7',
    border: '1px solid #E5E7EB',
    borderRadius: '4px',
    padding: '4px 10px',
    fontSize: '0.75rem',
    cursor: 'pointer',
    fontFamily: 'inherit',
    color: '#374151',
    whiteSpace: 'nowrap',
  },
  input: {
    width: '100%',
    padding: '8px 12px',
    border: '1px solid #D1D5DB',
    borderRadius: '6px',
    fontSize: '0.875rem',
    fontFamily: 'inherit',
    boxSizing: 'border-box',
    outline: 'none',
  },
  table: {
    width: '100%',
    borderCollapse: 'collapse',
  },
  th: {
    padding: '10px 20px',
    textAlign: 'left',
    fontSize: '0.75rem',
    fontWeight: 600,
    color: '#54585A',
    textTransform: 'uppercase',
    letterSpacing: '0.5px',
    borderBottom: '1px solid #E5E7EB',
    background: '#F9FAFB',
  },
  tr: {
    borderBottom: '1px solid #F3F4F6',
  },
  td: {
    padding: '12px 20px',
    fontSize: '0.875rem',
    verticalAlign: 'middle',
  },
  badge: {
    display: 'inline-block',
    padding: '2px 10px',
    borderRadius: '12px',
    fontSize: '0.6875rem',
    fontWeight: 600,
    textTransform: 'uppercase',
    letterSpacing: '0.5px',
  },
  select: {
    padding: '4px 8px',
    border: '1px solid #D1D5DB',
    borderRadius: '4px',
    fontSize: '0.75rem',
    fontFamily: 'inherit',
    background: '#FFFFFF',
    cursor: 'pointer',
    outline: 'none',
  },
  removeBtn: {
    padding: '4px 10px',
    background: '#DC2626',
    color: '#FFFFFF',
    border: 'none',
    borderRadius: '4px',
    fontSize: '0.75rem',
    fontWeight: 500,
    cursor: 'pointer',
    fontFamily: 'inherit',
  },
  confirmRemoveBtn: {
    padding: '4px 10px',
    background: '#991B1B',
    color: '#FFFFFF',
    border: 'none',
    borderRadius: '4px',
    fontSize: '0.75rem',
    fontWeight: 600,
    cursor: 'pointer',
    fontFamily: 'inherit',
  },
  cancelBtn: {
    padding: '4px 10px',
    background: '#FFFFFF',
    color: '#374151',
    border: '1px solid #D1D5DB',
    borderRadius: '4px',
    fontSize: '0.75rem',
    cursor: 'pointer',
    fontFamily: 'inherit',
  },
};

export default MembersTab;
