import React, { useState, useEffect, useCallback, useMemo } from 'react';
import { hasCommunityRole } from '../permissions';
import { supabase } from '../../supabaseClient';

function relativeTime(dateStr) {
  const now = Date.now();
  const then = new Date(dateStr).getTime();
  const diff = Math.max(0, now - then);
  const mins = Math.floor(diff / 60000);
  if (mins < 1) return 'just now';
  if (mins < 60) return `${mins}m ago`;
  const hrs = Math.floor(mins / 60);
  if (hrs < 24) return `${hrs}h ago`;
  const days = Math.floor(hrs / 24);
  if (days < 30) return `${days}d ago`;
  const months = Math.floor(days / 30);
  return `${months}mo ago`;
}

function FormsTab({ community, communityRole, onToast }) {
  const [submissions, setSubmissions] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [search, setSearch] = useState('');
  const [statusFilter, setStatusFilter] = useState('all');
  const [expandedId, setExpandedId] = useState(null);
  const [selected, setSelected] = useState(new Set());
  const [confirmDeleteId, setConfirmDeleteId] = useState(null);

  const canManage = hasCommunityRole(communityRole, 'commissioner');

  const fetchSubmissions = useCallback(async () => {
    try {
      const { data, error: fetchErr } = await supabase
        .from('form_submissions')
        .select('*')
        .eq('community_id', community.id)
        .order('created_at', { ascending: false });
      if (fetchErr) throw fetchErr;
      setSubmissions(data || []);
      setError(null);
    } catch (err) {
      setError(err.message);
      setSubmissions([]);
    } finally {
      setLoading(false);
    }
  }, [community.id]);

  useEffect(() => {
    fetchSubmissions();
  }, [fetchSubmissions]);

  // Filter by status and search
  const filtered = useMemo(() => {
    let list = submissions;
    if (statusFilter !== 'all') {
      list = list.filter(s => s.status === statusFilter);
    }
    if (search.trim()) {
      const q = search.toLowerCase();
      list = list.filter(s => {
        const d = s.data || {};
        return (d.name && d.name.toLowerCase().includes(q)) ||
          (d.email && d.email.toLowerCase().includes(q)) ||
          (d.subject && d.subject.toLowerCase().includes(q)) ||
          (d.message && d.message.toLowerCase().includes(q));
      });
    }
    return list;
  }, [submissions, statusFilter, search]);

  // Stats
  const totalCount = submissions.length;
  const unreadCount = submissions.filter(s => s.status === 'unread').length;
  const weekAgo = new Date(Date.now() - 7 * 24 * 60 * 60 * 1000).toISOString();
  const thisWeekCount = submissions.filter(s => s.created_at >= weekAgo).length;

  // Status filter counts
  const readCount = submissions.filter(s => s.status === 'read').length;
  const archivedCount = submissions.filter(s => s.status === 'archived').length;

  // Selection
  const allFilteredSelected = filtered.length > 0 && filtered.every(s => selected.has(s.id));

  const toggleSelect = (id) => {
    setSelected(prev => {
      const next = new Set(prev);
      if (next.has(id)) next.delete(id);
      else next.add(id);
      return next;
    });
  };

  const toggleSelectAll = () => {
    if (allFilteredSelected) {
      setSelected(prev => {
        const next = new Set(prev);
        filtered.forEach(s => next.delete(s.id));
        return next;
      });
    } else {
      setSelected(prev => {
        const next = new Set(prev);
        filtered.forEach(s => next.add(s.id));
        return next;
      });
    }
  };

  // Auto-mark as read when expanding an unread submission
  const handleExpand = async (sub) => {
    if (expandedId === sub.id) {
      setExpandedId(null);
      return;
    }
    setExpandedId(sub.id);
    if (sub.status === 'unread' && canManage) {
      const { error: updateErr } = await supabase
        .from('form_submissions')
        .update({ status: 'read' })
        .eq('id', sub.id);
      if (!updateErr) {
        setSubmissions(prev => prev.map(s => s.id === sub.id ? { ...s, status: 'read' } : s));
      }
    }
  };

  const handleUpdateStatus = async (id, status) => {
    try {
      const { error: updateErr } = await supabase
        .from('form_submissions')
        .update({ status })
        .eq('id', id);
      if (updateErr) throw updateErr;
      onToast?.(`Marked as ${status}`);
      setSubmissions(prev => prev.map(s => s.id === id ? { ...s, status } : s));
    } catch (err) {
      onToast?.('Failed to update: ' + err.message, 'error');
    }
  };

  const handleDelete = async (id) => {
    try {
      const { error: deleteErr } = await supabase
        .from('form_submissions')
        .delete()
        .eq('id', id);
      if (deleteErr) throw deleteErr;
      setConfirmDeleteId(null);
      setSelected(prev => {
        const next = new Set(prev);
        next.delete(id);
        return next;
      });
      if (expandedId === id) setExpandedId(null);
      onToast?.('Submission deleted');
      await fetchSubmissions();
    } catch (err) {
      onToast?.('Failed to delete: ' + err.message, 'error');
    }
  };

  const handleBulkArchive = async () => {
    const ids = [...selected];
    if (ids.length === 0) return;
    try {
      const { error: updateErr } = await supabase
        .from('form_submissions')
        .update({ status: 'archived' })
        .in('id', ids);
      if (updateErr) throw updateErr;
      setSelected(new Set());
      onToast?.(`${ids.length} submission(s) archived`);
      await fetchSubmissions();
    } catch (err) {
      onToast?.('Bulk archive failed: ' + err.message, 'error');
    }
  };

  const handleBulkDelete = async () => {
    const ids = [...selected];
    if (ids.length === 0) return;
    try {
      const { error: deleteErr } = await supabase
        .from('form_submissions')
        .delete()
        .in('id', ids);
      if (deleteErr) throw deleteErr;
      setSelected(new Set());
      if (ids.includes(expandedId)) setExpandedId(null);
      onToast?.(`${ids.length} submission(s) deleted`);
      await fetchSubmissions();
    } catch (err) {
      onToast?.('Bulk delete failed: ' + err.message, 'error');
    }
  };

  const handleExportCSV = () => {
    const rows = [['name', 'email', 'phone', 'subject', 'message', 'status', 'source_url', 'created_at']];
    const dataToExport = selected.size > 0
      ? submissions.filter(s => selected.has(s.id))
      : submissions;
    dataToExport.forEach(s => {
      const d = s.data || {};
      rows.push([
        d.name || '',
        d.email || '',
        d.phone || '',
        d.subject || '',
        d.message || '',
        s.status || '',
        s.source_url || '',
        s.created_at || '',
      ]);
    });
    const csv = rows.map(r => r.map(c => `"${String(c).replace(/"/g, '""')}"`).join(',')).join('\n');
    const blob = new Blob([csv], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `form-submissions-${community.slug}-${new Date().toISOString().slice(0, 10)}.csv`;
    a.click();
    URL.revokeObjectURL(url);
    onToast?.('CSV exported');
  };

  const formatDate = (dateStr) => {
    if (!dateStr) return '';
    return new Date(dateStr).toLocaleString('en-US', {
      month: 'short', day: 'numeric', year: 'numeric',
      hour: 'numeric', minute: '2-digit',
    });
  };

  const STATUS_FILTERS = [
    { key: 'all', label: 'All', count: totalCount },
    { key: 'unread', label: 'Unread', count: unreadCount },
    { key: 'read', label: 'Read', count: readCount },
    { key: 'archived', label: 'Archived', count: archivedCount },
  ];

  if (loading) {
    return (
      <div style={{ textAlign: 'center', padding: '60px 20px', color: '#54585A' }}>
        Loading submissions...
      </div>
    );
  }

  return (
    <div>
      <h1 style={styles.title}>Form Submissions</h1>
      <p style={styles.subtitle}>View and manage messages from your contact form widgets</p>

      {error && (
        <div style={styles.errorBanner}>
          Unable to load submissions: {error}
        </div>
      )}

      {/* Stats Row */}
      <div style={styles.statsRow}>
        <div style={styles.statCard}>
          <div style={styles.statValue}>{totalCount}</div>
          <div style={styles.statLabel}>Total Submissions</div>
        </div>
        <div style={styles.statCard}>
          <div style={{ ...styles.statValue, color: unreadCount > 0 ? '#2563EB' : '#041E42' }}>{unreadCount}</div>
          <div style={styles.statLabel}>Unread</div>
        </div>
        <div style={styles.statCard}>
          <div style={styles.statValue}>{thisWeekCount}</div>
          <div style={styles.statLabel}>This Week</div>
        </div>
      </div>

      {/* Status Filter Tabs */}
      <div style={styles.card}>
        <div style={{ padding: '12px 20px', display: 'flex', gap: '4px', flexWrap: 'wrap', borderBottom: '1px solid #E5E7EB' }}>
          {STATUS_FILTERS.map(f => (
            <button
              key={f.key}
              onClick={() => setStatusFilter(f.key)}
              style={{
                padding: '6px 14px',
                borderRadius: '20px',
                border: 'none',
                fontSize: '0.8125rem',
                fontWeight: 500,
                fontFamily: 'inherit',
                cursor: 'pointer',
                backgroundColor: statusFilter === f.key ? '#041E42' : '#F3F4F6',
                color: statusFilter === f.key ? '#FFFFFF' : '#374151',
                transition: 'all 0.15s',
              }}
            >
              {f.label}
              <span style={{
                marginLeft: '6px',
                fontSize: '0.6875rem',
                opacity: 0.8,
              }}>
                {f.count}
              </span>
            </button>
          ))}
        </div>

        {/* Toolbar */}
        <div style={{ padding: '12px 20px', display: 'flex', gap: '8px', alignItems: 'center', flexWrap: 'wrap' }}>
          <input
            type="text"
            value={search}
            onChange={e => setSearch(e.target.value)}
            placeholder="Search by name, email, subject, message..."
            style={{ ...styles.input, flex: 1, minWidth: '180px' }}
          />
          <button onClick={handleExportCSV} style={styles.secondaryBtn}>
            Export CSV
          </button>
          {canManage && selected.size > 0 && (
            <>
              <button onClick={handleBulkArchive} style={styles.secondaryBtn}>
                Archive ({selected.size})
              </button>
              <button onClick={handleBulkDelete} style={styles.dangerBtn}>
                Delete ({selected.size})
              </button>
            </>
          )}
        </div>
      </div>

      {/* Submission List */}
      {submissions.length === 0 && !error ? (
        <div style={styles.emptyState}>
          No form submissions yet. Add the contact form widget to your site to start receiving messages.
        </div>
      ) : filtered.length === 0 ? (
        <div style={styles.emptyState}>
          No submissions match your filters.
        </div>
      ) : (
        filtered.map(sub => {
          const d = sub.data || {};
          const isExpanded = expandedId === sub.id;
          const statusBadge = getStatusBadge(sub.status);

          return (
            <div key={sub.id} style={styles.card}>
              <div
                style={{
                  padding: '16px 20px',
                  cursor: 'pointer',
                  display: 'flex',
                  alignItems: 'flex-start',
                  gap: '12px',
                }}
                onClick={() => handleExpand(sub)}
              >
                {canManage && (
                  <input
                    type="checkbox"
                    checked={selected.has(sub.id)}
                    onChange={(e) => { e.stopPropagation(); toggleSelect(sub.id); }}
                    style={{ marginTop: '3px', flexShrink: 0 }}
                  />
                )}
                {sub.status === 'unread' && (
                  <div style={styles.unreadDot} />
                )}
                <div style={{ flex: 1, minWidth: 0 }}>
                  <div style={{ display: 'flex', alignItems: 'center', gap: '8px', marginBottom: '4px', flexWrap: 'wrap' }}>
                    <span style={{ fontWeight: 600, color: '#041E42', fontSize: '0.9375rem' }}>
                      {d.name || 'Anonymous'}
                    </span>
                    <span style={{ color: '#54585A', fontSize: '0.8125rem' }}>
                      {d.email || ''}
                    </span>
                    <span style={{ ...styles.badge, backgroundColor: statusBadge.bg, color: statusBadge.color }}>
                      {sub.status}
                    </span>
                    <span style={{ color: '#9CA3AF', fontSize: '0.75rem', marginLeft: 'auto', whiteSpace: 'nowrap' }}>
                      {relativeTime(sub.created_at)}
                    </span>
                  </div>
                  {d.subject && (
                    <div style={{ fontWeight: 500, color: '#374151', fontSize: '0.875rem', marginBottom: '4px' }}>
                      {d.subject}
                    </div>
                  )}
                  {!isExpanded && d.message && (
                    <div style={{ color: '#6B7280', fontSize: '0.8125rem', lineHeight: 1.4 }}>
                      {d.message.length > 100 ? d.message.slice(0, 100) + '...' : d.message}
                    </div>
                  )}
                </div>
                <div style={{ color: '#9CA3AF', fontSize: '0.875rem', flexShrink: 0, marginTop: '2px' }}>
                  {isExpanded ? '\u25B2' : '\u25BC'}
                </div>
              </div>

              {/* Expanded content */}
              {isExpanded && (
                <div style={{ padding: '0 20px 16px', borderTop: '1px solid #F3F4F6' }}>
                  <div style={{ padding: '16px 0 12px' }}>
                    <div style={{ color: '#374151', fontSize: '0.875rem', lineHeight: 1.6, whiteSpace: 'pre-wrap' }}>
                      {d.message}
                    </div>
                    <div style={{ marginTop: '12px', display: 'flex', flexWrap: 'wrap', gap: '16px', fontSize: '0.75rem', color: '#6B7280' }}>
                      {d.phone && (
                        <span>Phone: <strong style={{ color: '#374151' }}>{d.phone}</strong></span>
                      )}
                      {sub.source_url && (
                        <span>Source: <strong style={{ color: '#374151' }}>{sub.source_url.replace(/^https?:\/\//, '').slice(0, 50)}</strong></span>
                      )}
                      <span>Submitted: <strong style={{ color: '#374151' }}>{formatDate(sub.created_at)}</strong></span>
                      {sub.form_name && sub.form_name !== 'contact' && (
                        <span>Form: <strong style={{ color: '#374151' }}>{sub.form_name}</strong></span>
                      )}
                    </div>
                  </div>

                  {/* Actions */}
                  {canManage && (
                    <div style={{ display: 'flex', gap: '6px', paddingTop: '8px', borderTop: '1px solid #F3F4F6' }}>
                      {sub.status !== 'unread' && (
                        <button onClick={() => handleUpdateStatus(sub.id, 'unread')} style={styles.smallBtn}>
                          Mark Unread
                        </button>
                      )}
                      {sub.status !== 'archived' && (
                        <button onClick={() => handleUpdateStatus(sub.id, 'archived')} style={styles.smallBtn}>
                          Archive
                        </button>
                      )}
                      {sub.status === 'archived' && (
                        <button onClick={() => handleUpdateStatus(sub.id, 'read')} style={styles.smallBtn}>
                          Unarchive
                        </button>
                      )}
                      {confirmDeleteId === sub.id ? (
                        <div style={{ display: 'flex', gap: '4px', alignItems: 'center' }}>
                          <button onClick={() => handleDelete(sub.id)} style={styles.confirmDeleteBtn}>
                            Confirm
                          </button>
                          <button onClick={() => setConfirmDeleteId(null)} style={styles.cancelBtn}>
                            Cancel
                          </button>
                        </div>
                      ) : (
                        <button onClick={() => setConfirmDeleteId(sub.id)} style={styles.removeBtn}>
                          Delete
                        </button>
                      )}
                    </div>
                  )}
                </div>
              )}
            </div>
          );
        })
      )}
    </div>
  );
}

function getStatusBadge(status) {
  switch (status) {
    case 'unread': return { bg: '#DBEAFE', color: '#1E40AF' };
    case 'read': return { bg: '#F3F4F6', color: '#6B7280' };
    case 'archived': return { bg: '#F3F4F6', color: '#9CA3AF' };
    default: return { bg: '#F3F4F6', color: '#6B7280' };
  }
}

const styles = {
  title: {
    color: '#041E42',
    fontSize: '1.5rem',
    marginBottom: '0.5rem',
    fontWeight: 600,
  },
  subtitle: {
    color: '#54585A',
    marginBottom: '1.5rem',
  },
  statsRow: {
    display: 'flex',
    gap: '1rem',
    marginBottom: '1rem',
  },
  statCard: {
    flex: 1,
    background: '#FFFFFF',
    borderRadius: '8px',
    border: '1px solid #E5E7EB',
    padding: '16px 20px',
    textAlign: 'center',
  },
  statValue: {
    fontSize: '1.5rem',
    fontWeight: 700,
    color: '#041E42',
  },
  statLabel: {
    fontSize: '0.75rem',
    color: '#54585A',
    textTransform: 'uppercase',
    letterSpacing: '0.5px',
    marginTop: '2px',
  },
  card: {
    background: '#FFFFFF',
    borderRadius: '8px',
    border: '1px solid #E5E7EB',
    marginBottom: '0.75rem',
    overflow: 'hidden',
  },
  input: {
    padding: '8px 12px',
    border: '1px solid #D1D5DB',
    borderRadius: '6px',
    fontSize: '0.875rem',
    fontFamily: 'inherit',
    boxSizing: 'border-box',
    outline: 'none',
  },
  badge: {
    display: 'inline-block',
    padding: '2px 10px',
    borderRadius: '12px',
    fontSize: '0.6875rem',
    fontWeight: 600,
    textTransform: 'uppercase',
    letterSpacing: '0.5px',
  },
  unreadDot: {
    width: '8px',
    height: '8px',
    borderRadius: '50%',
    backgroundColor: '#2563EB',
    flexShrink: 0,
    marginTop: '7px',
  },
  secondaryBtn: {
    padding: '8px 16px',
    backgroundColor: '#FFFFFF',
    color: '#374151',
    border: '1px solid #D1D5DB',
    borderRadius: '6px',
    fontSize: '0.8125rem',
    fontWeight: 500,
    cursor: 'pointer',
    fontFamily: 'inherit',
    whiteSpace: 'nowrap',
  },
  dangerBtn: {
    padding: '8px 16px',
    backgroundColor: '#DC2626',
    color: '#FFFFFF',
    border: 'none',
    borderRadius: '6px',
    fontSize: '0.8125rem',
    fontWeight: 500,
    cursor: 'pointer',
    fontFamily: 'inherit',
    whiteSpace: 'nowrap',
  },
  smallBtn: {
    padding: '4px 10px',
    background: '#FFFFFF',
    color: '#374151',
    border: '1px solid #D1D5DB',
    borderRadius: '4px',
    fontSize: '0.75rem',
    cursor: 'pointer',
    fontFamily: 'inherit',
    whiteSpace: 'nowrap',
  },
  removeBtn: {
    padding: '4px 10px',
    background: '#DC2626',
    color: '#FFFFFF',
    border: 'none',
    borderRadius: '4px',
    fontSize: '0.75rem',
    fontWeight: 500,
    cursor: 'pointer',
    fontFamily: 'inherit',
  },
  confirmDeleteBtn: {
    padding: '4px 10px',
    background: '#991B1B',
    color: '#FFFFFF',
    border: 'none',
    borderRadius: '4px',
    fontSize: '0.75rem',
    fontWeight: 600,
    cursor: 'pointer',
    fontFamily: 'inherit',
  },
  cancelBtn: {
    padding: '4px 10px',
    background: '#FFFFFF',
    color: '#374151',
    border: '1px solid #D1D5DB',
    borderRadius: '4px',
    fontSize: '0.75rem',
    cursor: 'pointer',
    fontFamily: 'inherit',
  },
  emptyState: {
    textAlign: 'center',
    padding: '60px 20px',
    color: '#54585A',
    fontSize: '0.875rem',
    background: '#FFFFFF',
    borderRadius: '8px',
    border: '1px solid #E5E7EB',
  },
  errorBanner: {
    padding: '10px 14px',
    borderRadius: '6px',
    backgroundColor: '#FEE2E2',
    color: '#991B1B',
    fontSize: '0.8125rem',
    marginBottom: '1rem',
  },
};

export default FormsTab;
