import React, { useState, useEffect } from 'react';
import { supabase } from '../supabaseClient';

/**
 * EmbedLeaderboard
 *
 * Standalone leaderboard screen for the embed widget.
 * Tries community_leaderboards table first, falls back to
 * client-side aggregation from the games table.
 */
function EmbedLeaderboard({ communityId, currentUserId, onBack }) {
  const [rows, setRows] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    if (!communityId) return;

    let cancelled = false;

    const fetchLeaderboard = async () => {
      setLoading(true);
      setError(null);

      // Try community_leaderboards first
      const { data: viewData, error: viewErr } = await supabase
        .from('community_leaderboards')
        .select('user_id, username, avg_percentage, games_played')
        .eq('community_id', communityId)
        .order('rank', { ascending: true })
        .limit(10);

      if (!viewErr && viewData && viewData.length > 0) {
        if (!cancelled) {
          setRows(viewData);
          setLoading(false);
        }
        return;
      }

      // Fallback: aggregate from games table client-side
      const { data: games, error: gamesErr } = await supabase
        .from('games')
        .select('user_id, score, total_questions')
        .eq('community_id', communityId)
        .not('user_id', 'is', null);

      if (gamesErr) {
        if (!cancelled) {
          setError('Could not load leaderboard');
          setLoading(false);
        }
        return;
      }

      // Group by user_id
      const userMap = {};
      (games || []).forEach(g => {
        if (!userMap[g.user_id]) {
          userMap[g.user_id] = { user_id: g.user_id, totalScore: 0, totalQuestions: 0, gamesPlayed: 0 };
        }
        userMap[g.user_id].totalScore += g.score;
        userMap[g.user_id].totalQuestions += g.total_questions;
        userMap[g.user_id].gamesPlayed += 1;
      });

      const aggregated = Object.values(userMap)
        .map(u => ({
          user_id: u.user_id,
          avg_percentage: u.totalQuestions > 0 ? Math.round((u.totalScore / u.totalQuestions) * 1000) / 10 : 0,
          games_played: u.gamesPlayed,
          username: null, // will fetch below
        }))
        .sort((a, b) => b.avg_percentage - a.avg_percentage)
        .slice(0, 10);

      // Fetch usernames from profiles
      if (aggregated.length > 0) {
        const userIds = aggregated.map(u => u.user_id);
        const { data: profiles } = await supabase
          .from('profiles')
          .select('id, username')
          .in('id', userIds);

        const profileMap = {};
        (profiles || []).forEach(p => { profileMap[p.id] = p.username; });
        aggregated.forEach(u => { u.username = profileMap[u.user_id] || null; });
      }

      if (!cancelled) {
        setRows(aggregated);
        setLoading(false);
      }
    };

    fetchLeaderboard();
    return () => { cancelled = true; };
  }, [communityId]);

  const rankLabel = (rank) => {
    if (rank === 1) return '\u{1F947}';
    if (rank === 2) return '\u{1F948}';
    if (rank === 3) return '\u{1F949}';
    return `#${rank}`;
  };

  return (
    <div style={{ maxWidth: '600px', margin: '0 auto', padding: '1.5rem 0' }}>
      {/* Header with back button */}
      <div style={{
        display: 'flex',
        alignItems: 'center',
        marginBottom: '1.25rem',
        gap: '0.75rem',
      }}>
        <button
          onClick={onBack}
          style={{
            background: 'none',
            border: 'none',
            color: 'var(--embed-text)',
            fontSize: '1.25rem',
            cursor: 'pointer',
            padding: '0.25rem',
            lineHeight: 1,
            opacity: 0.7,
          }}
          aria-label="Back"
        >
          
        </button>
        <h2 style={{ margin: 0, fontSize: '1.25rem', fontWeight: '700' }}>
          Leaderboard
        </h2>
      </div>

      {/* Loading */}
      {loading && (
        <div style={{ textAlign: 'center', padding: '2rem', opacity: 0.5 }}>
          Loading leaderboard...
        </div>
      )}

      {/* Error */}
      {error && (
        <div style={{ textAlign: 'center', padding: '2rem', opacity: 0.6, fontSize: '0.9rem' }}>
          {error}
        </div>
      )}

      {/* Empty state */}
      {!loading && !error && rows.length === 0 && (
        <div style={{
          textAlign: 'center',
          padding: '3rem 1rem',
          opacity: 0.5,
          fontSize: '0.95rem',
        }}>
          <div style={{ fontSize: '2rem', marginBottom: '0.5rem' }}></div>
          No games yet. Be the first to play!
        </div>
      )}

      {/* Leaderboard rows */}
      {!loading && !error && rows.length > 0 && (
        <div>
          {rows.map((entry, idx) => {
            const rank = idx + 1;
            const isCurrentUser = currentUserId && entry.user_id === currentUserId;

            return (
              <div
                key={entry.user_id || idx}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                  padding: '0.75rem',
                  marginBottom: '0.375rem',
                  borderRadius: '8px',
                  backgroundColor: isCurrentUser
                    ? 'color-mix(in srgb, var(--embed-primary) 15%, var(--embed-surface))'
                    : 'var(--embed-surface)',
                  border: isCurrentUser
                    ? '2px solid var(--embed-primary)'
                    : '2px solid transparent',
                  transition: 'background-color 0.2s ease',
                }}
              >
                {/* Rank */}
                <div style={{
                  width: '2.5rem',
                  fontWeight: '700',
                  fontSize: rank <= 3 ? '1.1rem' : '0.9rem',
                  textAlign: 'center',
                  flexShrink: 0,
                }}>
                  {rankLabel(rank)}
                </div>

                {/* Username */}
                <div style={{ flex: 1, fontSize: '0.9rem', paddingLeft: '0.25rem' }}>
                  {entry.username || 'Anonymous'}
                  {isCurrentUser && (
                    <span style={{ opacity: 0.5, fontSize: '0.75rem', marginLeft: '0.25rem' }}>(you)</span>
                  )}
                </div>

                {/* Stats */}
                <div style={{ textAlign: 'right', flexShrink: 0 }}>
                  <div style={{ fontWeight: '600', fontSize: '0.9rem' }}>{entry.avg_percentage}%</div>
                  <div style={{ opacity: 0.5, fontSize: '0.7rem' }}>
                    {entry.games_played} game{entry.games_played !== 1 ? 's' : ''}
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      )}
    </div>
  );
}

export default EmbedLeaderboard;
