import React, { useState } from 'react';
import { supabase } from '../../supabaseClient';
import { hasCommunityRole } from '../permissions';

const TIER_OPTIONS = [
  { value: 'lite', label: 'Lite', description: 'Basic quiz embed management', color: '#54585A' },
  { value: 'pro', label: 'Pro', description: 'Adds member management & announcements', color: '#4A90D9' },
  { value: 'platform', label: 'Platform', description: 'Full trivia platform features', color: '#041E42' },
];

function SettingsTab({ community, communityRole, onToast }) {
  const [name, setName] = useState(community.name || '');
  const [description, setDescription] = useState(community.description || '');
  const [webhookUrl, setWebhookUrl] = useState(community.settings?.webhook_url || '');
  const [saving, setSaving] = useState(false);
  const [webhookTesting, setWebhookTesting] = useState(false);
  const [showPayloadDoc, setShowPayloadDoc] = useState(false);
  const [showDeleteConfirm, setShowDeleteConfirm] = useState(false);
  const [deleteInput, setDeleteInput] = useState('');
  const [deleting, setDeleting] = useState(false);
  const [tier, setTier] = useState(community.community_tier || 'lite');
  const [savingTier, setSavingTier] = useState(false);

  const isOwner = hasCommunityRole(communityRole, 'owner');
  const isCommissioner = hasCommunityRole(communityRole, 'commissioner');

  const handleSaveTier = async (newTier) => {
    setTier(newTier);
    setSavingTier(true);
    try {
      const { error } = await supabase
        .from('communities')
        .update({ community_tier: newTier })
        .eq('id', community.id);
      if (error) throw error;
      community.community_tier = newTier;
      onToast?.(`Community tier updated to ${newTier.charAt(0).toUpperCase() + newTier.slice(1)}`);
    } catch (err) {
      setTier(community.community_tier || 'lite');
      onToast?.('Failed to update tier: ' + err.message, 'error');
    } finally {
      setSavingTier(false);
    }
  };

  const handleSaveInfo = async () => {
    if (!name.trim()) {
      onToast?.('Community name is required', 'error');
      return;
    }
    setSaving(true);
    try {
      const { error } = await supabase
        .from('communities')
        .update({ name: name.trim(), description: description.trim() || null })
        .eq('id', community.id);
      if (error) throw error;
      onToast?.('Community info updated');
    } catch (err) {
      onToast?.('Failed to save: ' + err.message, 'error');
    } finally {
      setSaving(false);
    }
  };

  const handleSaveWebhook = async () => {
    setSaving(true);
    try {
      const newSettings = { ...(community.settings || {}), webhook_url: webhookUrl.trim() || null };
      const { error } = await supabase
        .from('communities')
        .update({ settings: newSettings })
        .eq('id', community.id);
      if (error) throw error;
      community.settings = newSettings;
      onToast?.('Webhook URL updated');
    } catch (err) {
      onToast?.('Failed to save webhook: ' + err.message, 'error');
    } finally {
      setSaving(false);
    }
  };

  const handleTestWebhook = async () => {
    const url = webhookUrl.trim();
    if (!url) {
      onToast?.('Enter a webhook URL first', 'error');
      return;
    }
    setWebhookTesting(true);
    const testPayload = {
      event: 'game.completed',
      timestamp: new Date().toISOString(),
      test: true,
      community: { slug: community.slug, name: community.name },
      player: { name: 'Test Player', anonymous: false },
      result: { score: 8, total: 10, percentage: 80, streak: 3 },
    };
    try {
      await fetch(url, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(testPayload),
        mode: 'no-cors',
        signal: AbortSignal.timeout(5000),
      });
      onToast?.('Test webhook sent! Check your endpoint for the payload.');
    } catch (err) {
      onToast?.('Webhook test failed: ' + err.message, 'error');
    } finally {
      setWebhookTesting(false);
    }
  };

  const handleDeleteCommunity = async () => {
    if (deleteInput !== community.name) {
      onToast?.('Name does not match', 'error');
      return;
    }
    setDeleting(true);
    try {
      // Delete members, questions, then community
      await supabase.from('community_members').delete().eq('community_id', community.id);
      await supabase.from('community_questions').delete().eq('community_id', community.id);
      const { error } = await supabase.from('communities').delete().eq('id', community.id);
      if (error) throw error;
      onToast?.('Community deleted');
      // Redirect away from admin
      window.location.hash = '';
      window.location.reload();
    } catch (err) {
      onToast?.('Failed to delete: ' + err.message, 'error');
      setDeleting(false);
    }
  };

  const copyToClipboard = (text, label) => {
    navigator.clipboard.writeText(text).then(() => {
      onToast?.(`${label} copied!`);
    }).catch(() => {
      onToast?.('Failed to copy', 'error');
    });
  };

  const inviteCode = community.invite_code || '';
  const embedSnippet = `<script src="https://gstreet-ops.github.io/gstreet-ops-quiz-embed/quiz-embed.js"></script>\n<quiz-embed community="${community.slug}"></quiz-embed>`;
  const iframeSnippet = `<iframe src="https://gstreet-ops.github.io/gstreet-ops-quiz-embed/?community=${community.slug}" width="100%" height="600" frameborder="0"></iframe>`;
  const subscribeSnippet = `<script src="https://gstreet-ops.github.io/gstreet-ops-quiz-embed/gstreet-subscribe.js"></script>\n<gstreet-subscribe community="${community.slug}"></gstreet-subscribe>`;
  const contactSnippet = `<script src="https://gstreet-ops.github.io/gstreet-ops-quiz-embed/gstreet-contact.js"></script>\n<gstreet-contact community="${community.slug}"></gstreet-contact>`;

  return (
    <div>
      <h1 style={{ color: '#041E42', fontSize: '1.5rem', marginBottom: '0.5rem', fontWeight: 600 }}>
        Settings
      </h1>
      <p style={{ color: '#54585A', marginBottom: '1.5rem' }}>
        Manage configuration for {community.name}
      </p>

      {/* Community Info */}
      <Section title="Community Info">
        <FieldGroup label="Community Name">
          <input
            type="text"
            value={name}
            onChange={e => setName(e.target.value)}
            disabled={!isCommissioner}
            style={inputStyle}
          />
        </FieldGroup>
        <FieldGroup label="Description">
          <textarea
            value={description}
            onChange={e => setDescription(e.target.value)}
            disabled={!isCommissioner}
            rows={3}
            style={{ ...inputStyle, resize: 'vertical' }}
            placeholder="A short description of your community"
          />
        </FieldGroup>
        {isCommissioner && (
          <button onClick={handleSaveInfo} disabled={saving} style={primaryBtnStyle}>
            {saving ? 'Saving...' : 'Save Changes'}
          </button>
        )}
      </Section>

      {/* Community Tier */}
      {isCommissioner && (
        <Section title="Community Tier">
          <FieldGroup label="Current Tier">
            <TierBadge tier={tier} />
          </FieldGroup>
          <FieldGroup label="Change Tier">
            <select
              value={tier}
              onChange={e => handleSaveTier(e.target.value)}
              disabled={savingTier}
              style={inputStyle}
            >
              {TIER_OPTIONS.map(opt => (
                <option key={opt.value} value={opt.value}>
                  {opt.label}  {opt.description}
                </option>
              ))}
            </select>
            {savingTier && (
              <div style={{ fontSize: '0.75rem', color: '#54585A', marginTop: '4px' }}>
                Saving...
              </div>
            )}
          </FieldGroup>
        </Section>
      )}

      {/* Embed Configuration */}
      <Section title="Embed Configuration">
        <FieldGroup label="Invite Code">
          <div style={{ display: 'flex', gap: '8px', alignItems: 'center' }}>
            <code style={codeStyle}>{inviteCode}</code>
            <CopyButton onClick={() => copyToClipboard(inviteCode, 'Invite code')} />
          </div>
          <div style={{ fontSize: '0.75rem', color: '#54585A', marginTop: '4px' }}>
            Share this code so others can join your community
          </div>
        </FieldGroup>

        <FieldGroup label="Web Component Snippet">
          <div style={{ position: 'relative' }}>
            <pre style={preStyle}>{embedSnippet}</pre>
            <CopyButton
              onClick={() => copyToClipboard(embedSnippet, 'Embed snippet')}
              style={{ position: 'absolute', top: '8px', right: '8px' }}
            />
          </div>
        </FieldGroup>

        <FieldGroup label="iFrame Snippet">
          <div style={{ position: 'relative' }}>
            <pre style={preStyle}>{iframeSnippet}</pre>
            <CopyButton
              onClick={() => copyToClipboard(iframeSnippet, 'iFrame snippet')}
              style={{ position: 'absolute', top: '8px', right: '8px' }}
            />
          </div>
        </FieldGroup>

        <FieldGroup label="Community Slug">
          <div style={{ display: 'flex', gap: '8px', alignItems: 'center' }}>
            <code style={codeStyle}>{community.slug}</code>
            <CopyButton onClick={() => copyToClipboard(community.slug, 'Slug')} />
          </div>
        </FieldGroup>

        <FieldGroup label="Email Collector Widget">
          <div style={{ position: 'relative' }}>
            <pre style={preStyle}>{subscribeSnippet}</pre>
            <CopyButton
              onClick={() => copyToClipboard(subscribeSnippet, 'Subscribe snippet')}
              style={{ position: 'absolute', top: '8px', right: '8px' }}
            />
          </div>
        </FieldGroup>

        <FieldGroup label="Contact Form Widget">
          <div style={{ position: 'relative' }}>
            <pre style={preStyle}>{contactSnippet}</pre>
            <CopyButton
              onClick={() => copyToClipboard(contactSnippet, 'Contact snippet')}
              style={{ position: 'absolute', top: '8px', right: '8px' }}
            />
          </div>
        </FieldGroup>
      </Section>

      {/* Webhook */}
      {isCommissioner && (
        <Section title="Webhook">
          <FieldGroup label="Webhook URL">
            <input
              type="url"
              value={webhookUrl}
              onChange={e => setWebhookUrl(e.target.value)}
              style={inputStyle}
              placeholder="https://example.com/webhook"
            />
            <div style={{ fontSize: '0.75rem', color: '#54585A', marginTop: '4px' }}>
              Receive a POST request whenever a player completes a quiz
            </div>
          </FieldGroup>
          <div style={{ display: 'flex', gap: '8px', marginBottom: '16px' }}>
            <button onClick={handleSaveWebhook} disabled={saving} style={primaryBtnStyle}>
              {saving ? 'Saving...' : 'Save Webhook'}
            </button>
            <button
              onClick={handleTestWebhook}
              disabled={webhookTesting || !webhookUrl.trim()}
              style={{
                ...secondaryBtnStyle,
                opacity: webhookTesting || !webhookUrl.trim() ? 0.5 : 1,
                cursor: webhookTesting || !webhookUrl.trim() ? 'not-allowed' : 'pointer',
              }}
            >
              {webhookTesting ? 'Sending...' : 'Send Test'}
            </button>
          </div>
          <div>
            <button
              onClick={() => setShowPayloadDoc(!showPayloadDoc)}
              style={{
                background: 'none',
                border: 'none',
                color: '#4A90D9',
                fontSize: '0.8125rem',
                cursor: 'pointer',
                fontFamily: 'inherit',
                padding: 0,
                textDecoration: 'underline',
              }}
            >
              {showPayloadDoc ? 'Hide payload format ' : 'View payload format '}
            </button>
            {showPayloadDoc && (
              <pre style={{ ...preStyle, marginTop: '8px' }}>{JSON.stringify({
                event: 'game.completed',
                timestamp: '2026-02-28T12:00:00.000Z',
                community: { slug: 'my-quiz', name: 'My Quiz' },
                player: { name: 'Jane', anonymous: false },
                result: { score: 8, total: 10, percentage: 80, streak: 3 },
              }, null, 2)}</pre>
            )}
          </div>
        </Section>
      )}

      {/* Danger Zone */}
      {isOwner && (
        <Section title="Danger Zone" danger>
          <p style={{ fontSize: '0.875rem', color: '#54585A', marginBottom: '16px' }}>
            Deleting a community permanently removes all questions, games, and member data.
            This action cannot be undone.
          </p>
          {!showDeleteConfirm ? (
            <button
              onClick={() => setShowDeleteConfirm(true)}
              style={dangerBtnStyle}
            >
              Delete Community
            </button>
          ) : (
            <div style={{
              padding: '16px',
              background: '#FEF2F2',
              borderRadius: '6px',
              border: '1px solid #FECACA',
            }}>
              <p style={{ fontSize: '0.875rem', color: '#991B1B', marginBottom: '12px', fontWeight: 500 }}>
                Type "{community.name}" to confirm deletion:
              </p>
              <input
                type="text"
                value={deleteInput}
                onChange={e => setDeleteInput(e.target.value)}
                style={{ ...inputStyle, marginBottom: '12px' }}
                placeholder={community.name}
              />
              <div style={{ display: 'flex', gap: '8px' }}>
                <button
                  onClick={handleDeleteCommunity}
                  disabled={deleteInput !== community.name || deleting}
                  style={{
                    ...dangerBtnStyle,
                    opacity: deleteInput !== community.name ? 0.5 : 1,
                    cursor: deleteInput !== community.name ? 'not-allowed' : 'pointer',
                  }}
                >
                  {deleting ? 'Deleting...' : 'Confirm Delete'}
                </button>
                <button
                  onClick={() => { setShowDeleteConfirm(false); setDeleteInput(''); }}
                  style={secondaryBtnStyle}
                >
                  Cancel
                </button>
              </div>
            </div>
          )}
        </Section>
      )}
    </div>
  );
}

function Section({ title, children, danger }) {
  return (
    <div style={{
      background: '#FFFFFF',
      borderRadius: '8px',
      border: danger ? '1px solid #FECACA' : '1px solid #E5E7EB',
      marginBottom: '1.5rem',
    }}>
      <div style={{
        padding: '16px 20px',
        borderBottom: danger ? '1px solid #FECACA' : '1px solid #E5E7EB',
      }}>
        <h2 style={{
          margin: 0,
          fontSize: '1rem',
          fontWeight: 600,
          color: danger ? '#DC2626' : '#041E42',
        }}>
          {title}
        </h2>
      </div>
      <div style={{ padding: '20px' }}>
        {children}
      </div>
    </div>
  );
}

function FieldGroup({ label, children }) {
  return (
    <div style={{ marginBottom: '16px' }}>
      <label style={{
        display: 'block',
        fontSize: '0.8125rem',
        fontWeight: 500,
        color: '#374151',
        marginBottom: '6px',
      }}>
        {label}
      </label>
      {children}
    </div>
  );
}

function CopyButton({ onClick, style }) {
  return (
    <button
      onClick={onClick}
      style={{
        background: '#F5F5F7',
        border: '1px solid #E5E7EB',
        borderRadius: '4px',
        padding: '4px 10px',
        fontSize: '0.75rem',
        cursor: 'pointer',
        fontFamily: 'inherit',
        color: '#374151',
        whiteSpace: 'nowrap',
        ...style,
      }}
    >
      Copy
    </button>
  );
}

function TierBadge({ tier }) {
  const config = TIER_OPTIONS.find(t => t.value === tier) || TIER_OPTIONS[0];
  return (
    <span style={{
      display: 'inline-block',
      padding: '4px 12px',
      borderRadius: '12px',
      fontSize: '0.75rem',
      fontWeight: 600,
      color: '#FFFFFF',
      backgroundColor: config.color,
      textTransform: 'uppercase',
      letterSpacing: '0.5px',
    }}>
      {config.label}
    </span>
  );
}

const inputStyle = {
  width: '100%',
  padding: '8px 12px',
  border: '1px solid #D1D5DB',
  borderRadius: '6px',
  fontSize: '0.875rem',
  fontFamily: 'inherit',
  boxSizing: 'border-box',
  outline: 'none',
};

const codeStyle = {
  padding: '6px 12px',
  background: '#F5F5F7',
  borderRadius: '4px',
  fontSize: '0.875rem',
  fontFamily: 'monospace',
  letterSpacing: '1px',
};

const preStyle = {
  background: '#F5F5F7',
  padding: '12px',
  borderRadius: '6px',
  fontSize: '0.75rem',
  fontFamily: 'monospace',
  whiteSpace: 'pre-wrap',
  wordBreak: 'break-all',
  margin: 0,
  lineHeight: 1.5,
};

const primaryBtnStyle = {
  padding: '8px 20px',
  background: '#041E42',
  color: '#FFFFFF',
  border: 'none',
  borderRadius: '6px',
  fontSize: '0.8125rem',
  fontWeight: 500,
  cursor: 'pointer',
  fontFamily: 'inherit',
};

const secondaryBtnStyle = {
  padding: '8px 20px',
  background: '#FFFFFF',
  color: '#374151',
  border: '1px solid #D1D5DB',
  borderRadius: '6px',
  fontSize: '0.8125rem',
  fontWeight: 500,
  cursor: 'pointer',
  fontFamily: 'inherit',
};

const dangerBtnStyle = {
  padding: '8px 20px',
  background: '#DC2626',
  color: '#FFFFFF',
  border: 'none',
  borderRadius: '6px',
  fontSize: '0.8125rem',
  fontWeight: 500,
  cursor: 'pointer',
  fontFamily: 'inherit',
};

export default SettingsTab;
