import { supabase } from '../supabaseClient';

/**
 * Fire-and-forget email helpers for the embed admin.
 * Calls the send-email Edge Function. Errors are logged to console
 * without blocking the UI.
 */

async function send(body) {
  try {
    const { data: { session } } = await supabase.auth.getSession();
    if (!session) {
      console.error('Email send skipped: no active session');
      return { success: false, error: 'No active session' };
    }
    const { data, error } = await supabase.functions.invoke('send-email', {
      body,
      headers: { Authorization: `Bearer ${session.access_token}` },
    });
    if (error) {
      console.error('Email send failed:', error);
      return { success: false, error: error.message };
    }
    return { success: true, data };
  } catch (err) {
    console.error('Email send failed:', err);
    return { success: false, error: err.message };
  }
}

export async function sendInvitationEmail(to, communityName, inviteCode, description, personalMessage) {
  return send({
    to,
    type: 'invitation',
    data: {
      communityName,
      inviteCode,
      description: description || '',
      personalMessage: personalMessage || '',
    },
  });
}
