import React, { useState, useEffect, useCallback, useMemo } from 'react';
import { hasCommunityRole } from '../permissions';
import { supabase } from '../../supabaseClient';

function SubscribersTab({ community, communityRole, onToast }) {
  const [subscribers, setSubscribers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [search, setSearch] = useState('');
  const [selected, setSelected] = useState(new Set());
  const [confirmDeleteId, setConfirmDeleteId] = useState(null);

  const canManage = hasCommunityRole(communityRole, 'commissioner');

  const fetchSubscribers = useCallback(async () => {
    try {
      const { data, error: fetchErr } = await supabase
        .from('subscribers')
        .select('*')
        .eq('community_id', community.id)
        .order('subscribed_at', { ascending: false });
      if (fetchErr) throw fetchErr;
      setSubscribers(data || []);
      setError(null);
    } catch (err) {
      setError(err.message);
      setSubscribers([]);
    } finally {
      setLoading(false);
    }
  }, [community.id]);

  useEffect(() => {
    fetchSubscribers();
  }, [fetchSubscribers]);

  const filtered = useMemo(() => {
    if (!search.trim()) return subscribers;
    const q = search.toLowerCase();
    return subscribers.filter(s =>
      (s.email && s.email.toLowerCase().includes(q)) ||
      (s.name && s.name.toLowerCase().includes(q))
    );
  }, [subscribers, search]);

  // Stats
  const totalCount = subscribers.length;
  const activeCount = subscribers.filter(s => s.status === 'active').length;
  const weekAgo = new Date(Date.now() - 7 * 24 * 60 * 60 * 1000).toISOString();
  const thisWeekCount = subscribers.filter(s => s.subscribed_at >= weekAgo).length;

  // Selection
  const allFilteredSelected = filtered.length > 0 && filtered.every(s => selected.has(s.id));

  const toggleSelect = (id) => {
    setSelected(prev => {
      const next = new Set(prev);
      if (next.has(id)) next.delete(id);
      else next.add(id);
      return next;
    });
  };

  const toggleSelectAll = () => {
    if (allFilteredSelected) {
      setSelected(prev => {
        const next = new Set(prev);
        filtered.forEach(s => next.delete(s.id));
        return next;
      });
    } else {
      setSelected(prev => {
        const next = new Set(prev);
        filtered.forEach(s => next.add(s.id));
        return next;
      });
    }
  };

  const handleUnsubscribe = async (id) => {
    try {
      const { error: updateErr } = await supabase
        .from('subscribers')
        .update({ status: 'unsubscribed' })
        .eq('id', id);
      if (updateErr) throw updateErr;
      onToast?.('Subscriber unsubscribed');
      await fetchSubscribers();
    } catch (err) {
      onToast?.('Failed to unsubscribe: ' + err.message, 'error');
    }
  };

  const handleDelete = async (id) => {
    try {
      const { error: deleteErr } = await supabase
        .from('subscribers')
        .delete()
        .eq('id', id);
      if (deleteErr) throw deleteErr;
      setConfirmDeleteId(null);
      setSelected(prev => {
        const next = new Set(prev);
        next.delete(id);
        return next;
      });
      onToast?.('Subscriber deleted');
      await fetchSubscribers();
    } catch (err) {
      onToast?.('Failed to delete: ' + err.message, 'error');
    }
  };

  const handleBulkUnsubscribe = async () => {
    const ids = [...selected];
    if (ids.length === 0) return;
    try {
      const { error: updateErr } = await supabase
        .from('subscribers')
        .update({ status: 'unsubscribed' })
        .in('id', ids);
      if (updateErr) throw updateErr;
      setSelected(new Set());
      onToast?.(`${ids.length} subscriber(s) unsubscribed`);
      await fetchSubscribers();
    } catch (err) {
      onToast?.('Bulk unsubscribe failed: ' + err.message, 'error');
    }
  };

  const handleBulkDelete = async () => {
    const ids = [...selected];
    if (ids.length === 0) return;
    try {
      const { error: deleteErr } = await supabase
        .from('subscribers')
        .delete()
        .in('id', ids);
      if (deleteErr) throw deleteErr;
      setSelected(new Set());
      onToast?.(`${ids.length} subscriber(s) deleted`);
      await fetchSubscribers();
    } catch (err) {
      onToast?.('Bulk delete failed: ' + err.message, 'error');
    }
  };

  const handleExportCSV = () => {
    const rows = [['email', 'name', 'source_url', 'subscribed_at', 'status']];
    const dataToExport = selected.size > 0
      ? subscribers.filter(s => selected.has(s.id))
      : subscribers;
    dataToExport.forEach(s => {
      rows.push([
        s.email || '',
        s.name || '',
        s.source_url || '',
        s.subscribed_at || '',
        s.status || '',
      ]);
    });
    const csv = rows.map(r => r.map(c => `"${String(c).replace(/"/g, '""')}"`).join(',')).join('\n');
    const blob = new Blob([csv], { type: 'text/csv' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `subscribers-${community.slug}-${new Date().toISOString().slice(0, 10)}.csv`;
    a.click();
    URL.revokeObjectURL(url);
    onToast?.('CSV exported');
  };

  const formatDate = (dateStr) => {
    if (!dateStr) return '';
    return new Date(dateStr).toLocaleDateString('en-US', {
      month: 'short', day: 'numeric', year: 'numeric',
    });
  };

  if (loading) {
    return (
      <div style={{ textAlign: 'center', padding: '60px 20px', color: '#54585A' }}>
        Loading subscribers...
      </div>
    );
  }

  return (
    <div>
      <h1 style={styles.title}>Subscribers</h1>
      <p style={styles.subtitle}>Manage email subscribers collected from your site widgets</p>

      {error && (
        <div style={styles.errorBanner}>
          Unable to load subscribers: {error}
        </div>
      )}

      {/* Stats Row */}
      <div style={styles.statsRow}>
        <div style={styles.statCard}>
          <div style={styles.statValue}>{totalCount}</div>
          <div style={styles.statLabel}>Total Subscribers</div>
        </div>
        <div style={styles.statCard}>
          <div style={styles.statValue}>{activeCount}</div>
          <div style={styles.statLabel}>Active</div>
        </div>
        <div style={styles.statCard}>
          <div style={styles.statValue}>{thisWeekCount}</div>
          <div style={styles.statLabel}>This Week</div>
        </div>
      </div>

      {/* Toolbar */}
      <div style={styles.card}>
        <div style={styles.cardBody}>
          <div style={{ display: 'flex', gap: '8px', alignItems: 'center', flexWrap: 'wrap' }}>
            <input
              type="text"
              value={search}
              onChange={e => setSearch(e.target.value)}
              placeholder="Search by email or name..."
              style={{ ...styles.input, flex: 1, minWidth: '180px' }}
            />
            <button onClick={handleExportCSV} style={styles.secondaryBtn}>
              Export CSV
            </button>
            {canManage && selected.size > 0 && (
              <>
                <button onClick={handleBulkUnsubscribe} style={styles.secondaryBtn}>
                  Unsubscribe ({selected.size})
                </button>
                <button onClick={handleBulkDelete} style={styles.dangerBtn}>
                  Delete ({selected.size})
                </button>
              </>
            )}
          </div>
        </div>
      </div>

      {/* Subscriber Table */}
      {subscribers.length === 0 && !error ? (
        <div style={styles.emptyState}>
          No subscribers yet. Add the email collector widget to your site to start collecting subscribers.
        </div>
      ) : (
        <div style={styles.card}>
          <div style={{ overflowX: 'auto' }}>
            <table style={styles.table}>
              <thead>
                <tr>
                  {canManage && (
                    <th style={{ ...styles.th, width: '40px' }}>
                      <input
                        type="checkbox"
                        checked={allFilteredSelected}
                        onChange={toggleSelectAll}
                      />
                    </th>
                  )}
                  <th style={styles.th}>Email</th>
                  <th style={styles.th}>Name</th>
                  <th style={styles.th}>Source</th>
                  <th style={styles.th}>Subscribed</th>
                  <th style={styles.th}>Status</th>
                  {canManage && <th style={{ ...styles.th, textAlign: 'right' }}>Actions</th>}
                </tr>
              </thead>
              <tbody>
                {filtered.map(sub => (
                  <tr key={sub.id} style={styles.tr}>
                    {canManage && (
                      <td style={styles.td}>
                        <input
                          type="checkbox"
                          checked={selected.has(sub.id)}
                          onChange={() => toggleSelect(sub.id)}
                        />
                      </td>
                    )}
                    <td style={styles.td}>{sub.email}</td>
                    <td style={{ ...styles.td, color: sub.name ? '#374151' : '#9CA3AF' }}>
                      {sub.name || ''}
                    </td>
                    <td style={{ ...styles.td, maxWidth: '200px', overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                      {sub.source_url ? (
                        <span title={sub.source_url} style={{ fontSize: '0.8125rem', color: '#54585A' }}>
                          {sub.source_url.replace(/^https?:\/\//, '').slice(0, 40)}
                        </span>
                      ) : ''}
                    </td>
                    <td style={styles.td}>{formatDate(sub.subscribed_at)}</td>
                    <td style={styles.td}>
                      <span style={{
                        ...styles.badge,
                        backgroundColor: sub.status === 'active' ? '#DEF7EC' : '#F3F4F6',
                        color: sub.status === 'active' ? '#03543F' : '#6B7280',
                      }}>
                        {sub.status}
                      </span>
                    </td>
                    {canManage && (
                      <td style={{ ...styles.td, textAlign: 'right' }}>
                        <div style={{ display: 'flex', gap: '4px', justifyContent: 'flex-end' }}>
                          {sub.status === 'active' && (
                            <button
                              onClick={() => handleUnsubscribe(sub.id)}
                              style={styles.smallBtn}
                            >
                              Unsubscribe
                            </button>
                          )}
                          {confirmDeleteId === sub.id ? (
                            <div style={{ display: 'flex', gap: '4px', alignItems: 'center' }}>
                              <button
                                onClick={() => handleDelete(sub.id)}
                                style={styles.confirmDeleteBtn}
                              >
                                Confirm
                              </button>
                              <button
                                onClick={() => setConfirmDeleteId(null)}
                                style={styles.cancelBtn}
                              >
                                Cancel
                              </button>
                            </div>
                          ) : (
                            <button
                              onClick={() => setConfirmDeleteId(sub.id)}
                              style={styles.removeBtn}
                            >
                              Delete
                            </button>
                          )}
                        </div>
                      </td>
                    )}
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}
    </div>
  );
}

const styles = {
  title: {
    color: '#041E42',
    fontSize: '1.5rem',
    marginBottom: '0.5rem',
    fontWeight: 600,
  },
  subtitle: {
    color: '#54585A',
    marginBottom: '1.5rem',
  },
  statsRow: {
    display: 'flex',
    gap: '1rem',
    marginBottom: '1rem',
  },
  statCard: {
    flex: 1,
    background: '#FFFFFF',
    borderRadius: '8px',
    border: '1px solid #E5E7EB',
    padding: '16px 20px',
    textAlign: 'center',
  },
  statValue: {
    fontSize: '1.5rem',
    fontWeight: 700,
    color: '#041E42',
  },
  statLabel: {
    fontSize: '0.75rem',
    color: '#54585A',
    textTransform: 'uppercase',
    letterSpacing: '0.5px',
    marginTop: '2px',
  },
  card: {
    background: '#FFFFFF',
    borderRadius: '8px',
    border: '1px solid #E5E7EB',
    marginBottom: '1rem',
    overflow: 'hidden',
  },
  cardBody: {
    padding: '20px',
  },
  input: {
    padding: '8px 12px',
    border: '1px solid #D1D5DB',
    borderRadius: '6px',
    fontSize: '0.875rem',
    fontFamily: 'inherit',
    boxSizing: 'border-box',
    outline: 'none',
  },
  table: {
    width: '100%',
    borderCollapse: 'collapse',
  },
  th: {
    padding: '10px 20px',
    textAlign: 'left',
    fontSize: '0.75rem',
    fontWeight: 600,
    color: '#54585A',
    textTransform: 'uppercase',
    letterSpacing: '0.5px',
    borderBottom: '1px solid #E5E7EB',
    background: '#F9FAFB',
  },
  tr: {
    borderBottom: '1px solid #F3F4F6',
  },
  td: {
    padding: '12px 20px',
    fontSize: '0.875rem',
    verticalAlign: 'middle',
  },
  badge: {
    display: 'inline-block',
    padding: '2px 10px',
    borderRadius: '12px',
    fontSize: '0.6875rem',
    fontWeight: 600,
    textTransform: 'uppercase',
    letterSpacing: '0.5px',
  },
  secondaryBtn: {
    padding: '8px 16px',
    backgroundColor: '#FFFFFF',
    color: '#374151',
    border: '1px solid #D1D5DB',
    borderRadius: '6px',
    fontSize: '0.8125rem',
    fontWeight: 500,
    cursor: 'pointer',
    fontFamily: 'inherit',
    whiteSpace: 'nowrap',
  },
  dangerBtn: {
    padding: '8px 16px',
    backgroundColor: '#DC2626',
    color: '#FFFFFF',
    border: 'none',
    borderRadius: '6px',
    fontSize: '0.8125rem',
    fontWeight: 500,
    cursor: 'pointer',
    fontFamily: 'inherit',
    whiteSpace: 'nowrap',
  },
  smallBtn: {
    padding: '4px 10px',
    background: '#FFFFFF',
    color: '#374151',
    border: '1px solid #D1D5DB',
    borderRadius: '4px',
    fontSize: '0.75rem',
    cursor: 'pointer',
    fontFamily: 'inherit',
    whiteSpace: 'nowrap',
  },
  removeBtn: {
    padding: '4px 10px',
    background: '#DC2626',
    color: '#FFFFFF',
    border: 'none',
    borderRadius: '4px',
    fontSize: '0.75rem',
    fontWeight: 500,
    cursor: 'pointer',
    fontFamily: 'inherit',
  },
  confirmDeleteBtn: {
    padding: '4px 10px',
    background: '#991B1B',
    color: '#FFFFFF',
    border: 'none',
    borderRadius: '4px',
    fontSize: '0.75rem',
    fontWeight: 600,
    cursor: 'pointer',
    fontFamily: 'inherit',
  },
  cancelBtn: {
    padding: '4px 10px',
    background: '#FFFFFF',
    color: '#374151',
    border: '1px solid #D1D5DB',
    borderRadius: '4px',
    fontSize: '0.75rem',
    cursor: 'pointer',
    fontFamily: 'inherit',
  },
  emptyState: {
    textAlign: 'center',
    padding: '60px 20px',
    color: '#54585A',
    fontSize: '0.875rem',
    background: '#FFFFFF',
    borderRadius: '8px',
    border: '1px solid #E5E7EB',
  },
  errorBanner: {
    padding: '10px 14px',
    borderRadius: '6px',
    backgroundColor: '#FEE2E2',
    color: '#991B1B',
    fontSize: '0.8125rem',
    marginBottom: '1rem',
  },
};

export default SubscribersTab;
